import Vue from 'vue'
import VueRouter from 'vue-router'
import Add from "@/views/Add";
import Index from '@/views/Index';
import NotPage from "@/views/Notpage";
import Ming from '../views/Ming'
import Type from '@/views/Type'

Vue.use(VueRouter)

const routes = [
    //设置index页面
  {
    path:'/',
    //配置项为
    redirect:'/index',
    //component: Index,
  },
  //首页
  { 
    name:'index',
    path:'/index',
    component:Index,
    //添加子路由
    children:[
      {
        name:'mingXi',
        path:'ming',
        component:Ming,
      },
      {
        name:'type',
        path:'type',
        component:Type,
      }
    ]
  },
  //添加页
  {
    name: 'add',
    path: '/add',
    component:Add,
  },
    //设置url错误时的页面
  {
    path: '*',
    component: NotPage,
  }

]

const router = new VueRouter({
  routes
})

export default router
