import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)
//选择是支出还是收入的数据
const type = {
  namespaced:true,
  mutations:{
    Out(state,val){
        if(val==='支出'&& state.seletOut===false){
          return
        }
        state.moneyType = val
        state.seletInter= !state.seletInter
        state.seletOut = !state.seletOut
    },
    Inter(state,val){
      if(val==='收入'&& state.seletInter===false){
        return
      }
        state.moneyType = val;
        state.seletInter= !state.seletInter
        state.seletOut = !state.seletOut
    }
},
  state:{
    moneyType:'支出',
    seletInter:true,
    seletOut:false,
},
}
//键盘数据
const keyBoard={
      namespaced:true,
      mutations:{
        getNum(state,val){
          //判断sum的长度不能超过16;设置sum为0时，不能在点击0键
          if(state.sum.length>=16 || state.sum ==='0' && val === 0){
              return
          }
          //sum中有点时，点不能重复使用
          if(val === '.'){
              if(state.sum.indexOf('.')>0){
                  return
              }
          }
          //设置sum为0时，不能在点击0键
          if(state.sum ==='0'){
              if(val ==='.'){
                  state.sum += val
              }else{
                  state.sum = val+''
              }
          }else{
              state.sum=state.sum +=val;
          }
          
      },
      //完成
      done(state){
          //读数据
          const arr =JSON.parse(localStorage.getItem('test')) 
          //创建数组
          const moneyData = arr || []
          //创建对象
          const note = {
            createTime:this.state.date.timeValue,
            iconName:this.state.iconType.icon,
            type:this.state.type.moneyType,
            text:this.state.iconType.moneyType,
            num:this.state.keyBoard.sum,
            info:this.state.myInput.note
          }
           if(note.num === '0'){
                alert('金额不能为零，请输入金额。')
                return
          }
          //数据永久化保存
          moneyData.push(note)
          localStorage.setItem('test',JSON.stringify(moneyData))
          alert('添加成功')
          //在store中实现路由跳转
          window.Vue.$router.push({
            name:'mingXi',
          })
          state.sum = '0'
      },
      //退格
      backspace(state,val){
          if(state.sum.length ===1){
              state.sum = '0'
              return
          }
          state.sum = state.sum.slice(0,-1)
      },
      //全删
      clear(state,val){
          state.sum = '0';
      }
      },
      state:{
        sum:'0',
      }
}
//icon图标数据
const iconType ={
    namespaced:true,
    mutations:{
      handler(state,icon){
        state.moneyType = icon.text
        state.icon = icon.iconName
        this.commit(`iconType/${icon.fun}`,icon)
    },
      changeAll(state,icon){
        //更改iconStyle的样式
          for (const item of state.slide) {
              for (const i of item) {
                    i.iconStyle = true
              }
          }
          icon.iconStyle = false
      },
      addIcon(state){
          //判断是添加页和项，还是就添加项
        const title = prompt('请输入（最多输入四个字)').slice(0,4)
        //判断title是否被占有
        for (const item of state.slide) {
          for (const i of item) {
            if(i.text === title){
              alert('标签名已被占用了，请重新输入')
              return
            }
          }
        }
        if(title){
              //创建一个对象
              const i = {
                      iconName:'el-icon-document',
                      text:title,
                      iconStyle:true,
                      fun:'changeAll'
                  }
              //寻找slide中最后一个数组
              const lastArr = state.slide[state.slide.length-1]
              if(lastArr.length<=9){
                  lastArr.splice(-1,0,i)
              }else if (lastArr.length === 10) {
                  //把添加icon替换为新添加的title
                  lastArr.splice(9,1,i)
                  //新建一页
                  const page = [
                      {
                          iconName:'el-icon-plus',
                          text:'添加标签',
                          iconStyle:true,
                          fun:'addIcon'
                      }
                  ]
                  state.slide.push(page)
                  console.log(state.slide)
                  console.log('添加的了新的一页')
              }
        }
      },
    },
    state:{
      moneyType:'一般',
      icon:'el-icon-document-remove',
      slide:[
          [
              {
                  iconName:'el-icon-document-remove',
                  text:'一般',
                  iconStyle:false,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-food',
                  text:'吃饭',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-truck',
                  text:'打车',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-school',
                  text:'酒店',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-goods',
                  text:'购物',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-grape',
                  text:'水果',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-lollipop',
                  text:'零食',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-video-camera',
                  text:'娱乐',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-user',
                  text:'社交',
                  iconStyle:true,
                  fun:'changeAll'
              },
              {
                  iconName:'el-icon-phone-outline',
                  text:'通讯',
                  iconStyle:true,
                  fun:'changeAll'
              },
          ],
          [
              {
                  iconName:'el-icon-plus',
                  text:'添加标签',
                  iconStyle:true,
                  fun:'addIcon'
              }
          ]
      ],
    }
}
//日期数据
const date = {
    namespaced:true,
    mutations:{
      gai(state,val){
        state.timeValue = val
      }
    },
    state:{
      timeValue:'',
    },
}
//input数据
const myInput ={
  namespaced:true,
  mutations:{
    chuan(state,val){
        if(val.length>10){
          state.note = val.slice(0,10)
        }else{
          state.note = val
        }
        
    }
  },
  state:{
    note:'',
  }
}
//bigBoss 所有数据汇总
const allData ={
    namespaced:true,
    //存在bug，年改变时因该调用月的方法。
    mutations:{
        receiveYear(state,val){
            //获取全部数据
            const localData = JSON.parse(localStorage.getItem('test'))
            //创建容器
            const demandData_Year = []
            //得到想要的数据;可优化代码
            if(localData){
                for (const item of localData) {
                    for (const keyName in item) {
                       if(keyName === 'createTime'){
                         if(item[keyName].indexOf(val.toString())>=0){
                            demandData_Year.push(item)
                         } 
                       }
                    }
                }
            }
          //动态添加数据
          Vue.set(this.state.allData,'demandData_Year',demandData_Year)
        },
        receiveMonth(state,val){
            const demandDate_month = []
            if(state.demandData_Year.length){
               for (const item of state.demandData_Year) {
                 for (const keyName in item) {
                  if (keyName === 'createTime') {
                    const month = item[keyName].split('-')[1]
                     if(month.indexOf('0')===0){
                        const a = month.slice(1)
                        if(a===val.toString()){
                          demandDate_month.push(item)
                      }
                     }else if(month===val.toString()){
                      demandDate_month.push(item)
                    }
                    
                    
                }
              }
            }
        }
            Vue.set(this.state.allData,'demandDate_month',demandDate_month)
        },
        receiveDay(state){
          //创建分组函数
          function groups(arr,fn){
              const group1 = {}
              arr.forEach( i => {
                const tagName = fn(i)
                group1[tagName] = group1[tagName] || []
                group1[tagName].push(i)
              });
                return group1
            }
            if(state.demandDate_month.length){
              const demandDate_day = groups(state.demandDate_month,(item)=>item.createTime)
              Vue.set(this.state.allData,'demandDate_day',demandDate_day)
            }else{
              const demandDate_day = {}
              Vue.set(this.state.allData,'demandDate_day',demandDate_day)
            }
            
        }
    },
    state:{},
    getters:{
        getMonthSum(state){
            //创建分组
            function groups(arr,fn){
              let group1 = {}
              arr.forEach( i => {
                const tagName = fn(i)
                group1[tagName] = group1[tagName] || []
                group1[tagName].push(parseFloat(i.num))
              });
                return group1
            }
            let income = 0
            let expend = 0
            let residue = 0
            if(state.demandDate_month){
              let a = groups(state.demandDate_month,item=>item.type)
              if(Object.keys(a).length){
                if(a['支出']){
                  a['支出'].forEach(item=>{
                    expend += item
                  })
                }
               if(a['收入']){
                  a['收入'].forEach(item=>{
                    income += item
                  })
               }
                residue = income - expend
              }
            }
         const monthSum ={income,expend,residue,}
         return monthSum
        },
        getDaySum(state){
          //创建分组并求和
          function groups(arr,fn){
            const group1 = {}
            //分组
            arr.forEach( i => {
              const tagName = fn(i)
              group1[tagName] = group1[tagName] || []
              group1[tagName].push(parseFloat(i.num))
            });
            //求和
            let income = 0
            let expend = 0
              if(group1['收入']){
                  group1['收入'].forEach(item =>{
                    income +=item
                })
              }
              if(group1['支出']){
                  group1['支出'].forEach(item =>{
                    expend += item
                  })
              }
            const sum = {income,expend}
              return sum
          }
          const b = {}
          if(Object.keys(state.demandDate_day).length){
            for (const key in state.demandDate_day) {
              b[key] = groups(state.demandDate_day[key],(item)=>item.type)
           }
          }
          return b
        },
        getYear(state){
            //创建分类并求和的函数
            function groups(list,fn){
                const m = {}
                list.forEach((item)=>{
                    const tagName = fn(item)
                    m[tagName] = m[tagName] || []
                    m[tagName].push(item)
                })
                //创建自己需要的数据结构
                return m
            }
            const statisticData = {} 
            if(state.demandData_Year.length){
              const test = groups(state.demandData_Year,(item)=>{
                const token = item.createTime.split('-')
                return token[1]
              })
              for (const key in test) {
                statisticData[key] = groups(test[key],item=>item.type)
                statisticData[key].expend = 0
                statisticData[key].income = 0
                for (const keyMonth in statisticData[key]) {
                  if(typeof statisticData[key][keyMonth] === 'object'){
                    statisticData[key][keyMonth].forEach(item =>{
                      if(keyMonth ==='支出'){
                       statisticData[key].expend +=parseFloat(item.num) 
                      }else if(keyMonth ==='收入'){
                       statisticData[key].income +=parseFloat(item.num) 
                   
                      }
                    })
                  }
                   
                }
              }
            }
            return statisticData
        }
    }
} 
export default new Vuex.Store({
  modules:{
    type,
    keyBoard,
    iconType,
    date,
    allData,
    myInput,
  }
})
