<template lang="html">
    <div>
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for=" page,index of slide" :key="index">
                    <div class="icons">
                        <div class="icon" v-for="icon,index of page" :key="index" @click="handler(icon)">
                            <el-button type="primary" :plain="icon.iconStyle" :icon="icon.iconName" :circle="true"></el-button>
                            <div class="text">{{icon.text}}</div> 
                        </div>
                    </div>
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>
<script>
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
import {mapState,mapMutations} from 'vuex'
export default {
    name:'MoneyType',
    
    computed:{
        ...mapState('iconType',['moneyType','slide'])
    },
    methods:{
        ...mapMutations('iconType',['handler'])
    },
    //初始化swiper组件库
    mounted() {
        
        let mySwiper = 0;
        
        if (mySwiper !== 0) {
            mySwiper.destroy();
        }
        new Swiper ('.swiper-container', {
            
            //loop: true, // 循环模式选项
            observer: true,//动态添加分页
            observeParents: true,//动态添加分页，
            // 如果需要分页器
            pagination: {
            el: '.swiper-pagination',
            },
        })
    },
}
</script>
<style lang="scss" scoped>
    .swiper-container{
        width: 100%;
        height: 250px;
    }
    .swiper-slide{
        >.icons{
            margin-top: 30px;
            height: 70%;
            display:flex;
            flex-wrap: wrap;
            align-items: space-around;
            >.icon{
                height: 40%;
                width: 20%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                >.text{
                    color: #333;
                    font-size: 14px;
                    margin: 5px 0;
                }
            }
        }
    }
</style>