<template lang="html">
    <div class="top">
            <div class="name">
                <GetDate></GetDate>
                <div>记账本</div>
            </div>
            <div class="info">
                <div class="outMoney">
                    <span class="text">支出(元)</span>
                    <div class="outNumber">{{getMonthSum.expend}}</div>
                </div>
                <div class="interMoney">
                    <span class="text">收入(元)</span>
                    <div class="outNumber">{{getMonthSum.income}}</div>
                </div>
                <div class="Money">
                    <span class="text">剩余(元)</span>
                    <div class="outNumber">{{getMonthSum.residue}}</div>
                </div>
            </div>
    </div>
</template>
<script>
import GetDate from './icon/GetDate.vue';
import {mapGetters} from 'vuex'
export default {
    name: "Top",
    components: { GetDate },
    computed:{
        ...mapGetters('allData',['getMonthSum'])
    }
}
</script>
<style lang="scss" scoped>
    .top{
        background-color: #272a3b;
        display: flex;
        flex-direction: column;
        color: #fff;
        >.name{
            color:#fff;
            font-size: 16px;
            text-align: center;
            line-height: 24px;
            padding: 10px 0;
            height: 44px;
            border-bottom: 1px solid #484b59;
        }
        >.info{
            display: flex;
            padding: 10px;
            justify-content: space-between;
            >.outMoney .interMoney .Money{
                color: inherit;
                display: flex;
                flex-direction: column;
                border-right: 1px dashed ;
            }
                .text{
                    font-size: 12px;
                    color:#98a0bd ;
                }
                .outNumber{
                    height: 45px;
                    line-height: 45px;
                    font-size: 20px;
                }
            
        }
    }
</style>