<template lang="html">
    <div class="board">
        <div class="input">
            <div class="clear">
                <Clear @click.native="clear"></Clear>
            </div>
            <div class="backSpace">
                <div class="sum">
                    {{sum}}
                </div>
                <BackSpace @click.native="backspace"></BackSpace>
            </div>
        </div>
        <div class="board-content">
            <div class="keys">
                <div class="num" @click="getNum(1)">1</div>
                <div class="num" @click="getNum(2)">2</div>
                <div class="num" @click="getNum(3)">3</div>
                <div class="num" @click="getNum(4)">4</div>
                <div class="num" @click="getNum(5)">5</div>
                <div class="num" @click="getNum(6)">6</div>
                <div class="num" @click="getNum(7)">7</div>
                <div class="num" @click="getNum(8)">8</div>
                <div class="num" @click="getNum(9)">9</div>
                <div class="num" @click="getNum(0)">0</div>
                <div class="num" @click="getNum('.')">.</div>
                <div class="num done"  @click="done">完成</div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState,mapMutations} from 'vuex'
import BackSpace from './icon/BackSpace.vue'
import Clear from './icon/Clear.vue'
export default {
    components: { Clear, BackSpace },
    name: "KyeBoard",
    computed:{
        ...mapState('keyBoard',['sum'])
    },
    methods:{
        ...mapMutations('keyBoard',['getNum','done','clear','backspace'])
    }
}
</script>
<style lang="scss" scoped>
    .board{
        display: flex;
        flex-direction: column;
        background-color: #f2f2f2;
        >.input{
            border-bottom: 1px solid rgb(243, 243, 243);
            text-align: right;
            font-size: 24px;
            padding: 5px 10px;
            display: flex;
            justify-content: space-between;
            >.backSpace{
                display: flex;
                >.sum{
                    margin-right: 5px;
                }
            }
        }
        >.board-content{
            display: flex;
            padding: 10px;
            >.keys{
                display: flex;
                font-size: 22px;
                flex-wrap: wrap;
                flex-grow: 1;
                >.num{
                    width: 33%;
                    background-color: #ffffff;
                    border: 1px rgb(238, 238, 238) solid;
                    text-align: center;
                    height: 48px;
                    line-height: 48px;
                }
                
            }
           
        }
    }
</style>