<template lang="html">
    <div>
        <div>
            <div ref="chart1" style="width:100%;height:376px"></div>
          </div>
    </div>
</template>
<script>
import {mapGetters,mapState} from 'vuex'
export default {
    name:'Type',
    mounted() {
      this.getEchartData1()
    },
    methods: {
      getEchartData1() {
        const chart1 = this.$refs.chart1
        if (chart1) {
          const myChart = this.$echarts.init(chart1)
          const option = {
            tooltip: {
              trigger: 'axis',
            },
            title:{
                text:`统计图`,
                textStyle:{
                    color: '#6f717a',
                }
            },
            legend: {
              data: ['支出', '收入']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月','八月','九月','十月','十一月','十二月']
            },
            yAxis: {
              type: 'value'
            },
            series: [
              {
                name: '支出',
                type: 'line',
                data: [
                    this.getYear['01'] ? this.getYear['01'].expend:0,
                    this.getYear['02'] ? this.getYear['02'].expend:0,
                    this.getYear['03'] ? this.getYear['03'].expend:0,
                    this.getYear['04'] ? this.getYear['04'].expend:0, 
                    this.getYear['05'] ? this.getYear['05'].expend:0,
                    this.getYear['06'] ? this.getYear['06'].expend:0,
                    this.getYear['07'] ? this.getYear['07'].expend:0,
                    this.getYear['08'] ? this.getYear['08'].expend:0, 
                    this.getYear['09'] ? this.getYear['09'].expend:0,
                    this.getYear[10] ? this.getYear[10].expend:0,
                    this.getYear[11] ? this.getYear[11].expend:0,
                    this.getYear[12] ? this.getYear[12].expend:0
                ]
              },
              {
                name: '收入',
                type: 'line',
                data: [
                    this.getYear['01'] ? this.getYear['01'].income:0,
                    this.getYear['02'] ? this.getYear['02'].income:0,
                    this.getYear['03'] ? this.getYear['03'].income:0,
                    this.getYear['04'] ? this.getYear['04'].income:0, 
                    this.getYear['05'] ? this.getYear['05'].income:0,
                    this.getYear['06'] ? this.getYear['06'].income:0,
                    this.getYear['07'] ? this.getYear['07'].income:0,
                    this.getYear['08'] ? this.getYear['08'].income:0, 
                    this.getYear['09'] ? this.getYear['09'].income:0,
                    this.getYear[10] ? this.getYear[10].income:0,
                    this.getYear[11] ? this.getYear[11].income:0,
                    this.getYear[12] ? this.getYear[12].income:0
                ]
              },
              
            ]

          }
          myChart.setOption(option)
          myChart.on('click',function(params){
             console.log('yes')
          })
          window.addEventListener("resize", function() {
            myChart.resize()
          })
        }
        this.$on('hook:destroyed',()=>{
          window.removeEventListener("resize", function() {
            myChart.resize();
          });
        })
      },
 
    },
    computed:{
        ...mapState('allData',['demandData_Year']),
        ...mapGetters('allData',['getYear'])
    },
    watch:{
        getYear:{
            deep:true,
            handler(){
                this.getEchartData1()
            }
        }
    }
}
</script>
<style lang="scss">
    
</style>