        <input v-model="a" type="text" placeholder="添加备注" @change="info">
    </div>
</template>
<script>
export default {
    name:'Input',
    data(){
        return{
            a:'',
        }
    },
    methods:{
        info(){
            this.$store.commit('myInput/chuan',this.a)
        }
    }
}
</script>
