<template lang="html">
    <div class="top-date" ref="main">
        <div class="tip-date">{{year}} 年 {{month}} 月</div>
        <i class="el-icon-caret-bottom icon" @click="changeStyle"></i>
        <div v-if="show" class="console">
            <div class="title">请选择日期</div>
            <div class="date">
                <div class="year">
                    <el-input-number v-model="year" :min="0" size="medium" @change="transmitYear(year)"></el-input-number>
                    <div class="text">年</div>
                </div>
                <div class="month">
                    <el-input-number v-model="month" :min="1" :max="12" size="medium" @change="transmitMonth(month)"></el-input-number>
                    <div class="text">月</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapMutations} from 'vuex'
export default {
    name:'GetDate',
    data(){
        return{
            show:false,
            year:this.getYear(),
            month:this.getMonth(),
        }
    },
    //绑定事件
    mounted(){
        document.addEventListener('click',this.cancel)
    },
    methods:{
        getYear(){
            let now = new Date();
            let year = now.getFullYear(); //得到年份
            this.$store.commit('allData/receiveYear',year)
            return year;
        },
        getMonth(){
            let now = new Date();
            let month = now.getMonth() +1;//得到月
            this.$store.commit('allData/receiveMonth',month)
            this.$store.commit('allData/receiveDay')
            return month
        },
        changeStyle(){
            this.show = ! this.show
        },
        cancel(e){
            if(!this.$refs.main.contains(e.target)){
                if(this.show){
                    this.show = ! this.show
                }
            }  
        }, 
        transmitYear(year){
            this.$store.commit('allData/receiveYear',year)
            this.$store.commit('allData/receiveMonth',this.month)
            this.$store.commit('allData/receiveDay')
        },
        transmitMonth(month){
            this.$store.commit('allData/receiveMonth',month)
            this.$store.commit('allData/receiveDay')
        }
    },
    //解绑事件
    beforeDestroy(){
        document.removeEventListener('click',this.cancel)
    }
}
</script>
<style lang="scss" scoped>
    .top-date{
        position: absolute;
        display: flex;
        margin-left: 10px;
        >.tip-date{
            width: 7em;
        }
        >.icon{
            line-height:24px;
            margin-left: 2px;
        }
        >.console{
            position: relative;
            top: 25px;
            left: -90px;
            width: 200px;
            box-shadow:0 0 10px rgba(0, 0, 0, 0.4);
            background-color: #FFF;
            border-radius: 5px;
            display: flex;
            flex-direction: column;
            padding: 5px 0;
            >.title{
                border-bottom: 1px solid #f5f7fa;
                color: #000;
                margin: 0px 10px;
            }
            >.date{
                >.year{
                    display: flex;
                    color:#000;
                    margin:10px;
                    >.text{
                        margin-left: 10px;
                        line-height: 36px;
                    }
                }
                >.month{
                    display: flex;
                    color:#000;
                    margin:10px;
                    >.text{
                        margin-left: 10px;
                        line-height: 36px;
                    }
                }
            }
        }
    }
</style>