<template lang="html">
    <div>
        <div class="block">
            <el-date-picker
              :clearable="true"
              v-model="Time"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </div>
    </div>
</template>
<script>
//解决date残留bug
export default {
    name:'Date',
    data(){
        return{
          Time:this.NowTime(),
        }
    },
    //设置默认值的用法。
    methods: {
        NowTime() {
        let now = new Date();
        let year = now.getFullYear(); //得到年份
        let month = now.getMonth(); //得到月份
        let date = now.getDate(); //得到日期
        month = month + 1;
        month = month.toString().padStart(2, "0");
        date = date.toString().padStart(2, "0");
        let defaultDate = `${year}-${month}-${date}`;
        this.$store.commit('date/gai',defaultDate)
        return defaultDate;
        
      }
    },
    watch:{
      Time:{
        deep:true,
        handler(newVal){
            if(newVal){
               this.$store.commit('date/gai',newVal)
            }
        }
      }
    }
}
</script>
