<template lang="html">
    <div>
        <div v-if="Object.keys(demandDate_day).length">
            <div class="m-body">
                <div class="day" v-for="tag,index of demandDate_day" :key="index" >
                    <div class="day-data">
                        <div class="today">{{tag[0].createTime}}</div>
                        <div class="money">支出{{getDaySum[tag[0].createTime].expend}} 收入{{getDaySum[tag[0].createTime].income}}</div>
                    </div>
                    <div class="day-event" v-for="i,index of tag" :key="index">
                        <div class="token">
                            <el-button size="small" class="icon" type="primary"  :icon="i.iconName" circle></el-button>
                            <div class="icon-name">{{i.text}}</div>
                            <div class="text">{{i.info}}</div>
                        </div>
                        <div class="num">{{i.type}} {{i.num}}</div>
                    </div> 
                </div>
            </div>
        </div>
        <div v-else class="tip">
            本月暂无记录，请添加账单。
        </div>
    </div>
   
</template>
<script>
import {mapState,mapGetters} from 'vuex'
export default {
    name: "Ming",
    computed:{
        ...mapState('allData',['demandDate_day']),
        ...mapGetters('allData',['getDaySum'])
    },
}
</script>
<style lang="scss">
    .m-body{
        display: flex;
        flex-direction: column;
        overflow: auto;
        background-color: #fff;
        >.day{
            display: flex;
            flex-direction: column;
            >.day-data{
                display: flex;
                justify-content: space-between;
                padding: 8px 16px;
                font-size: 12px;
                color: #7c7a7a;
                border-bottom: 1px solid #e1e1e1;
            }
            >.day-event{
                display: flex;
                justify-content: space-between;
                padding: 8px 16px;
                border-bottom: 1px solid #e1e1e1;
                font-size: 16px;
                line-height: 32px;
                >.token{
                    display: flex;
                    font-size: 16px;
                    >.icon{
                        margin-right: 10px;
                    }
                    >.icon-name{
                        margin-right: 10px;
                    }
                }
            }
        }
    }
    .tip{
        margin: auto;
        text-align: center;
        font-size: 18px;
    }
</style>